/*
    ===   Custom Style File   ===
*/
/*
    This file included in each .scss-file
*/
/*===   CSS variables*/
:root {
  --cPrimary: #007e3f;
  --cPrimaryDark: #006e3f;
  --cPrimaryLight: #00ae3f;
  --cPrimaryLightest: #ccc;
  --cPrimaryTracing: #e5f4ff;
  --cFontPrimary: #fff;
  --cSecondary: #008641;
  --cGray: #ccc;
  --cGrayDark: #3e3e3e;
  --cGrayLight: #f3f3f5;
  --cError: #dc3545;
  --cValid: #28ac45;
  --cWhite: #fff;
  --cBlack: #000;
}

body.theme-custom {
  /*
      here override css-variables:
      example: --сSecondary: #008641;
              to
              --сSecondary: #1c8fc2;
  */
}

/*===   SCSS constants*/
/*===   Main styles*/
html {
  height: 100%;
  font-size: 14px;
  fill: var(--cPrimary);
}

body {
  font-family: "Montserrat", sans-serif;
}

/*hide spinners from number input*/
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/*placeholders color*/
::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--cGray);
  opacity: 1;
  /* Firefox */
  font-style: italic;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: var(--cGray);
  font-style: italic;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: var(--cGray);
  font-style: italic;
}

select option:disabled {
  font-style: italic;
}

hr {
  width: 100%;
  border-color: var(--cGray);
}
hr.bold {
  border-width: 3px;
  border-color: var(--cBlack);
}

@media (min-width: 1366px) {
  .container {
    max-width: 1300px;
  }
}
@media (min-width: 1600px) {
  .container {
    max-width: 1500px;
  }
}
/*===   Additional bootstrap-like classes*/
.hidden {
  display: none;
}

.link {
  color: var(--cPrimary);
  text-decoration: underline;
  cursor: pointer;
}

ul.reset {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
}

.flex-shrink-0 {
  flex-shrink: 0;
}

.primary-color {
  color: var(--cPrimary);
}

.primary-background {
  background-color: var(--cPrimary);
  color: var(--cFontPrimary);
}

.secondary-color {
  color: var(--cSecondary);
}

.secondary-background {
  background-color: var(--cSecondary);
  color: var(--cFontPrimary);
}

.hover-scale, .vc-color-picker button {
  transition: 0.3s;
}
.hover-scale:hover, .vc-color-picker button:hover {
  transform: scale(1.1);
}

.form-error {
  border-radius: 3px;
  color: var(--cError);
  border: 1px solid var(--cError);
  background: var(--cWhite);
  padding: 20px;
}

.toolbar label {
  font-size: 1.2rem;
  color: var(--cPrimary);
  font-weight: bold;
}

.address-bar {
  color: var(--cGray);
}
.address-bar .address span, .address-bar .address a {
  font-size: 1.2rem;
}

/*===   Default controls override*/
.btn {
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.3rem;
  padding: 5px 20px;
}
.btn.reset {
  padding: 0;
  background: transparent;
}
.btn.reset:active, .btn.reset:focus {
  box-shadow: none !important;
  outline: none;
}
.btn.short {
  padding: 5px 0;
}
.btn:disabled, .btn.disabled {
  opacity: 0.35;
}
.btn:active, .btn:focus {
  box-shadow: inset 1px 1px 10px rgba(0, 0, 0, 0.2) !important;
  outline: none;
}

.btn-primary, .btn-primary:disabled {
  background-color: var(--cPrimary);
  border-color: var(--cPrimary);
  color: var(--cFontPrimary);
}
.btn-primary:hover, .btn-primary:disabled:hover {
  background-color: var(--cPrimaryLight);
  border-color: var(--cPrimaryLight);
}
.btn-primary:focus, .btn-primary:active, .btn-primary:disabled:focus, .btn-primary:disabled:active {
  background-color: var(--cPrimaryDark) !important;
  border-color: var(--cPrimaryDark) !important;
}

.buttons-panel.full-length {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.buttons-panel.full-length button {
  flex: 1 1 0;
}
.buttons-panel.bottom-fixed {
  position: fixed;
  background: var(--cPrimaryTracing);
  border-top: 2px solid var(--cPrimary);
  right: 0;
  bottom: 0;
  left: 0;
  padding: 15px;
  transition: 0.3s;
  animation: fadeIn 0.7s ease-in both;
  opacity: 0;
}
.buttons-panel button {
  border-radius: 0;
}
.buttons-panel button:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.buttons-panel button:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
