.badge-icon {
  position: absolute;
  top: -5px;
  right: -15px;
  background-color: var(--cError);
  border-radius: 50%;
  font-size: 14px;
  height: 20px;
  width: 20px;
  text-align: center;
  line-height: 1.2;
  animation: 3s heartbeat ease-in-out infinite;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.material-input {
  margin-top: 20px;
  position: relative;
  width: 150px;
}
.material-input input {
  border: 0;
  padding: 4px 0;
  border-bottom: 1px solid var(--cGray);
  background-color: transparent;
  font: 15px/24px "Lato", Arial, sans-serif;
  width: 100%;
  box-sizing: border-box;
  letter-spacing: 1px;
}
.material-input input:focus {
  outline: none;
}
.material-input input ~ .material-border {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--cPrimary);
  transition: 0.4s;
}
.material-input input:focus ~ .material-border,
.material-input input.has-content ~ .material-border {
  width: 100%;
  transition: 0.4s;
}
.material-input input ~ label {
  position: absolute;
  left: 0;
  width: 100%;
  top: 9px;
  color: var(--cPrimary);
  transition: 0.3s;
  letter-spacing: 0.5px;
  z-index: -1;
}
.material-input input:focus ~ label,
.material-input input.has-content ~ label {
  top: -16px;
  font-size: 0.8rem;
  color: var(--cPrimary);
  transition: 0.3s;
}

.material-checkbox * {
  transition: 0.3s;
}
.material-checkbox *:before, .material-checkbox *:after {
  transition: 0.3s;
}
.material-checkbox label {
  position: relative;
  cursor: pointer;
  padding: 0;
}
.material-checkbox label:before {
  content: "";
  margin-right: 10px;
  display: inline-block;
  vertical-align: text-top;
  width: 20px;
  height: 20px;
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.12);
  background: var(--cWhite);
}
.material-checkbox input[type=checkbox] {
  position: absolute;
  opacity: 0;
}
.material-checkbox input[type=checkbox]:hover + label:before {
  background-color: var(--cPrimary);
}
.material-checkbox input[type=checkbox]:focus + label:before {
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
}
.material-checkbox input[type=checkbox]:checked + label:before {
  background-color: var(--cPrimary);
}
.material-checkbox input[type=checkbox]:disabled + label {
  color: var(--cGray);
  cursor: auto;
}
.material-checkbox input[type=checkbox]:disabled + label:before {
  box-shadow: none;
  background: var(--cGray);
}
.material-checkbox input[type=checkbox]:checked + label:after {
  content: "";
  position: absolute;
  left: 5px;
  top: 9px;
  background: var(--cWhite);
  width: 2px;
  height: 2px;
  box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
  transform: rotate(45deg);
}

.search-panel {
  width: 100%;
  position: relative;
}
.search-panel input {
  font-size: 16px;
  padding-right: 55px;
  padding-bottom: 10px;
}
.search-panel input ~ label {
  font-size: 22px;
  top: 0;
  color: #ccc;
}
.search-panel .btn-search {
  position: absolute;
  top: -5px;
  right: 10px;
  border-radius: 50%;
  border: 1px solid #192599;
  color: #192599;
  width: 40px;
  height: 40px;
  font-size: 36px;
  background: transparent;
}
.search-panel .btn-search i {
  display: block;
  margin-top: -8px;
  margin-left: -1px;
}

.address-bar {
  color: #ccc;
}
.address-bar .address span, .address-bar .address a {
  font-size: 16px;
}

.main-loader {
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.2);
}
.main-loader .spinner {
  position: relative;
  display: inline-block;
  width: 90px;
  height: 90px;
  border: 2px solid;
  border-radius: 50%;
  animation: spin 0.75s infinite linear;
  border-color: transparent;
  border-top-color: var(--cPrimary);
  animation-duration: 1.2s;
}
.main-loader .spinner:before, .main-loader .spinner:after {
  left: -2px;
  top: -2px;
  display: none;
  position: absolute;
  content: "";
  width: inherit;
  height: inherit;
  border: inherit;
  border-radius: inherit;
  display: inline-block;
  border-color: transparent;
  border-top-color: var(--cPrimary);
  animation-duration: 1.2s;
}
.main-loader .spinner:before {
  transform: rotate(120deg);
}
.main-loader .spinner:after {
  transform: rotate(240deg);
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.search-panel-host {
  flex: 1 1 0;
  padding: 0 15px;
}

.search-panel {
  position: relative;
  width: 100%;
  padding-right: 55px;
}
.search-panel input {
  font-size: 1.3rem;
  padding-bottom: 4px;
}
.search-panel input ~ label {
  font-size: 1.5rem;
  top: 0;
  color: #ccc;
}
.search-panel input:focus ~ .material-border,
.search-panel input.has-content ~ .material-border {
  width: calc(100% - 55px);
}
.search-panel .btn-search {
  position: absolute;
  top: -5px;
  right: 10px;
  border-radius: 50%;
  border: 1px solid var(--cPrimary);
  color: var(--cPrimary);
  width: 40px;
  height: 40px;
  font-size: 36px;
  background: transparent;
}
.search-panel .btn-search i {
  display: block;
  margin-top: -4px;
  margin-left: -6px;
  -webkit-text-stroke: 2px var(--cWhite);
}

.vc-table {
  width: 100%;
  text-align: center;
}
.vc-table tr {
  transition: 0.3s;
}
.vc-table tr.selected {
  background: var(--cGray);
}
.vc-table tr:hover {
  background: var(--cPrimaryTracing);
}
.vc-table tr th {
  background: var(--cGrayLight);
  padding: 20px 0;
  text-align: center;
  color: var(--cPrimary);
  font-size: 1.2rem;
}
.vc-table tr th span {
  display: block;
  border-right: 2px solid #ccc;
  padding: 10px 0;
}
.vc-table tr th:last-of-type span {
  border-right: none;
}
.vc-table tr td {
  padding: 15px;
}
.vc-table tr td .cell-content {
  display: flex;
  justify-content: center;
}

.vc-color-picker button {
  background: url("/themes/assets/images/color-wheel-icon.png") no-repeat;
  background-size: contain;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid var(--cWhite);
  cursor: pointer;
}
.vc-color-picker button:active, .vc-color-picker button:focus {
  box-shadow: none;
  outline: none;
}
.vc-color-picker canvas {
  border-radius: 50%;
  border: 3px solid var(--cPrimary);
}
.vc-color-picker .dropdown-menu {
  background: transparent;
  border: none;
}
.vc-color-picker .color-picker {
  display: inline-block;
  position: relative;
}
.vc-color-picker .color-picker input {
  display: none;
}
.vc-color-picker .color-picker .canvas-wrapper {
  border-radius: 1000px;
  overflow: hidden;
}
.vc-color-picker .color-picker .indicator {
  top: calc(50% - 2rem);
  left: 50%;
  display: block;
  position: absolute;
  background-color: transparent;
  transform: translate3d(-50%, -2rem, 0);
  pointer-events: none;
}
.vc-color-picker .color-picker .indicator .selected-color {
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 30%;
  border-radius: 1000px;
  z-index: -1;
  background-color: #fff;
  -webkit-filter: drop-shadow(0 5px 15px rgba(0, 0, 0, 0.5));
  filter: drop-shadow(0 5px 15px rgba(0, 0, 0, 0.5));
}

body {
  height: 100%;
  background: url("/themes/assets/static/bundle/assets/images/login_bg.jpg") no-repeat;
  background-size: cover;
}

main {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
}

.window {
  border: 1px solid var(--cGray);
  padding: 25px 35px;
  border-radius: 10px;
  box-shadow: 7px 7px 3px rgba(0, 0, 0, 0.3);
  background: rgba(255, 255, 255, 0.9);
  max-width: 400px;
  animation: fadeIn 1s ease-in both;
  opacity: 0;
}
.window .material-input {
  width: 100%;
}
.window .forgot-link {
  font-size: 14px;
  text-decoration: underline;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-5%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}